body {
	margin: 0;
	padding: 0;
	overflow: hidden;
	font-family: 'Roboto', sans-serif;
	color: #ffffff;
	background: rgb(118,168,181);
	background: -webkit-gradient(left top, right top, color-stop(0%, rgb(118,168,181)), color-stop(41%, rgb(86,138,153)), color-stop(100%, rgb(49,111,128)));
	background: linear-gradient(to right, rgb(118,168,181) 0%, rgb(86,138,153) 41%, rgb(49,111,128) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#76a8b5', endColorstr='#316f80', GradientType=1 );
}

h1 {
	background: rgba(170, 170, 170, 0.3);
	border-radius: 10px;
	padding: 0px 10px 0px 10px;
	pointer-events: none;
	position: absolute;
	margin: 30%;
	font-size: 600%;
	font-weight: 100;
	letter-spacing: -3px;
	color: #afffff;
	z-index: 1 !important
}

span {
	color: #dfedff;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

